
<template>
  <div>
    <ModalComponent
        v-show="isShowModal2"
        :show="isShowModal2"
        :scrollable="true"
        header-id="modalHeader"
        body-id="modalBody"
        @close="toggleModal"
    />
    <table class="table">
      <div class="table__head"><tr>
        <th class="table__head__cell"><h3 class="widthSmoll">Шифр</h3></th>
        <th class="table__head__cell "><h3 class="widthLarge">Обозначение</h3></th>
        <th class="table__head__cell "><h3 class="widthSmoll"></h3>Наименование</th>


      </tr>
      </div>


      <div class="table__content">
        <div v-for='item in standards' :key="item.id" class="table__content__item">
          <tr>
            <td class="table__content__item__text "><h3 class="widthSmoll">{{ item.cipher }}</h3></td>
            <td class="table__content__item__text "><h3 class="widthLarge">{{ item.designation }}</h3></td>
            <td class="table__content__item__text "><h3 class="widthSmoll">{{ item.title }}</h3></td>
            <button @click="toggleModal">
              Редактировать
            </button>
            <button type="button">Удалить</button>

          </tr>
        </div>
      </div>
    </table>
  </div>
</template>
<script>
import ModalComponent from "@/components/ModalComponent.vue";
import {AXIOS} from "../../http-common";

export default {
  name: 'StandardComponent',
  components: {
    ModalComponent,
  },
  data(){
    return{
      standards:[],
      isShowModal: false,
    }
  }
  ,
  methods: {
    loadGreeting() {
      AXIOS.get('/standards/all')
          .then(response => {
            this.$data.standards = response.data;

          })
          .catch(error => {
            console.log('ERROR: ' + error.response.data);
          })

    },
    toggleModal() {
      this.isShowModal = !this.isShowModal;
    },
  },
  mounted() {
    this.loadGreeting();
  }

}
</script>
<style scoped lang="scss">

</style>